

/* eslint-disable no-unused-vars */


import React, { Component } from "react"
import MetaTags from 'react-meta-tags';
import {
  Card,
  Col,
  Container,
  Row,
  CardBody,
  CardHeader,
  CardTitle,
  FormGroup,
  Label,
  Button,
  Form,
  Input,
  InputGroupAddon,
  InputGroupText,
  InputGroup,
  Breadcrumb,
  BreadcrumbItem,
} from "reactstrap"

import {
  CButton,
  CCard,
  CCardHeader,
  CCardBody,
  CCardFooter,
  CCol,
  CContainer,
  CForm,
  CInput,
  CInputGroup,
  CInputGroupPrepend,
  CInputGroupText,
  CRow
} from '@coreui/react'
import Select from "react-select"
import axios from 'axios'
import  { Redirect } from 'react-router-dom'

import { French } from "flatpickr/dist/l10n/fr.js";

// import Flatpickr from "react-flatpickr"
import Flatpickr from "flatpickr";

import DatePicker from "react-datepicker";
import { registerLocale, setDefaultLocale } from  "react-datepicker";
import es from 'date-fns/locale/es';
import $ from 'jquery';
import conf from '../../../conf.js';
import ReactSelect from 'react-select'
import makeAnimated from 'react-select/animated';
import ville from "../../dict/ville.js";
import { storage } from "../../../firebase/firebase";
import ModalMessage from "../../pages/messages/ModalMessage"
const animatedComponents = makeAnimated();


// const cities = require('all-the-cities');

registerLocale('es', es)


  const optionGroup = [
    {
      
      options: [
        { label: "PDF", value: "PDF" },
        { label: "MP4", value: "MP4" },
        { label: "JPEG", value: "JPEG" },
        { label: "MP3", value: "MP3" },
        { label: "EXCEL", value: "EXCEL" },
        
      ]
    },
    
  ]
  const optionTypeEntreprise = [
    {
      options: [
        { label: "SRL", value: "srl" },
        { label: "SA", value: "sa" },
        { label: "PME", value: "pme" }
      ]
    },
  ]
  const optionPaye = [
    {
      options: [
        { label: "Payé", value: true },
        { label: "Impayé", value: false },
      ]
    },
  ]
  const optionArchive = [
    {
      options: [
        { label: "Archivé", value: true },
        { label: "Pas archivé", value: false },
      ]
    },
  ]

class ModifierEquipe extends Component {
    constructor(props) {
        super(props)
        this.state = {
          user:{},
          ville:"",
          ok:"",
          tournois:{},
          paye:false,
          archive:false,
          equipe:{},
          type:"",
          files_preview:"",
          files_name:{file:""},
          multimedia:{file:""},
          multiUpdate:{},
          holder_paye:"",
          holder_archive:"",
          holder_type:"",
          show:false,
          success:true,
          boutton:"OK",
          message:"Equipe Modifié avec succès !!",
          titre:"Message modification de équipe"
        }
        this.handleChangeType.bind(this)
        this.handleChangePaye.bind(this)
        this.handleChangeArchive.bind(this)
    }

    componentDidMount() {
      
      let  data=  JSON.parse(localStorage.getItem('token_auth'))
      var id_equipe=localStorage.getItem("idEquipe")
      var id_tournoi=localStorage.getItem("idTournoi")


      let equipe=[]
      const requestOptions = {
        method: 'GET',
        headers: { 
          'Authorization':'Bearer '+data.token,
    
        },
      };
  
      fetch(conf.url+"equipe/equipes/"+id_equipe,requestOptions)
      .then((response) => response.json())
      .then((data) => {
        console.log("data",data);
        this.setState({equipe:data})
    
        $("#nomequipe").val(data.nomEquipe);
        $("#nomentreprise").val(data.nomEntreprise);
        this.setState({archive:data.archive,paye:data.paye})
        this.setState({type:data.typeEntreprise,holder_paye:(data.paye?"Payé":"Impayé"),holder_archive:(data.archive?"Archivé":"Pas Archivé"),holder_type:data.typeEntreprise})
        if(data.multimedia!=null){
          storage.ref("images")
          .child(data.multimedia.file).getDownloadURL()
          .then((url) => {
            
              this.setState({files_preview:url})
              this.setState({multiUpdate:data.multimedia})
          
          })
         }

        })

        fetch(conf.url+"tournoi/tournois/"+id_tournoi,requestOptions)
        .then((response) => response.json())
        .then((data) => {
          console.log("data",data)
          this.setState({tournois:data})
       
          })

       
    }

    handleUpdateEquipe=()=>{
      let array=[this.state.tournois]
      var media=null
      let  token=  JSON.parse(localStorage.getItem('token_auth'))
      var data= {
        "id":this.state.equipe.id,
        "nomEquipe": $("#nomequipe").val(),
        "nomEntreprise": $("#nomentreprise").val(),
        "typeEntreprise":this.state.type,
        "paye":this.state.paye,
        "archive": this.state.archive,
        "tournois":array,
        "multimedia":this.state.multiUpdate
      
    
      }


      if(this.state.files_name.file!=""){
        media=this.state.multimedia
        storage.ref("images/"+this.state.files_name.fileName).put(this.state.files_name.file).then(()=>{})
        data= {
          "id":this.state.equipe.id,
          "nomEquipe": $("#nomequipe").val(),
          "nomEntreprise": $("#nomentreprise").val(),
          "typeEntreprise":this.state.type,
          "paye":this.state.paye,
          "archive": this.state.archive,
          "tournois":array,
          "multimedia":media, 
        
       }
      }
  
      console.log("data a transmettre",data)
  
      const requestOptions = {
        method: 'POST',
        headers: { 'Content-Type': 'application/json','Accept': 'application/json',
        'Authorization':'Bearer '+token.token,
      },
        body: JSON.stringify(data)
    };
  
    fetch(conf.url+"equipe/update",requestOptions)
    .then((response) => response.json())
    .then((data) => {
       console.log("okkk====>",data)
       

       try{
        if(data.id){
         this.setState({show:true,success:true});
        }else{
         this.setState({show:true,success:false});
        }
     }catch(err){
       this.setState({show:true,success:false});
     }
    
     }
     
    );
      
    }  
    handleChangePaye = (e) => {
     
      console.log("adate =====>",e)
      try{
        this.setState({paye:e.value})
       
      }catch(ee){
  
      }
    
    }

    handleChangeArchive = (e) => {
     
      console.log("adate =====>",e)
      try{
        this.setState({archive:e.value})
       
      }catch(ee){
  
      }
    
    }
    
    handleChangeType = (e) => {
     
      console.log("adate =====>",e)
      try{
        this.setState({type:e.label})
       
      }catch(ee){
  
      }
    
    }

     handleRetour=()=>{
      this.setState({ok:<Redirect to='/detail-tournoi'/>})
    }

    handleValidate=()=>{
      if(this.state.success){
        this.setState({ok: <Redirect to='/detail-Tournoi'/>});
      }else{
        this.setState({show:false})
      }
      
    }
  
   
  render() {

    const { pick } = this.state
    const { selectedGroup } = this.state
    const { selectedLangue } = this.state
    const animatedComponents = makeAnimated();
    
  return (
        <>
          <React.Fragment>
            <div className="page-content">
             {this.state.ok}
             <ModalMessage
              show={this.state.show}
              success={this.state.success}
              boutton={this.state.boutton}
              handleValidate={this.handleValidate}
              message={this.state.message}
              titre={this.state.titre}
              onCloseClick={() => this.setState({ show: false })}
              />
            <Container fluid={false} className="justify-content-center">

              <Row className="justify-content-center">
              <Col md="12" lg="12" xl="12">

                {/* <Row ClassName="mb-3">
                  <Col md="8">
                      <div className="page-title-box d-flex align-items-center justify-content-between">
                        <h4 className="mb-0 font-size-18">Nouveau compte</h4>
                      </div>
                  </Col>
                </Row> */}
     
                

                <Card ClassName="mt-5">
                  {/* <CCardHeader className="p-4"> */}
                    <h1 className="p-4" >Modifier détails de l'équipe</h1>
                  {/* </CCardHeader> */}

                  <CardBody>
                    <Row> 
                      <Col md="6">
                        <label>Nom de l'équipe</label>
                        <Input
                          className="form-control"
                          type="text"
                          id="nomequipe"
                          placeholder="Nom de l'équipe"
                        />
                      </Col>
                      <Col md="6">
                        <label>Nom de l'entreprise</label>
                        <Input
                          className="form-control"
                          type="text"
                          id="nomentreprise"
                          placeholder="Nom de l'entreprise"
                        />
                      </Col>
                    </Row>

                    <Row className="mt-4">
                      
                      <Col md="4">
                        <label>Type d'entreprise</label>
                        <ReactSelect
                          closeMenuOnSelect={true}
                          components={animatedComponents}
                           onChange={this.handleChangeType}
                          options={optionTypeEntreprise}
                          placeholder={this.state.holder_type}
                        />
                      </Col>
                      <Col md="4">
                        <label>Payé / Impayé</label>
                        <ReactSelect
                          closeMenuOnSelect={true}
                          components={animatedComponents}
                          onChange={this.handleChangePaye}
                          options={optionPaye}
                          placeholder={this.state.holder_paye}
                        />
                      </Col>
                      <Col md="4">
                        <label>Archivé / Non archivé</label>
                        <ReactSelect
                          closeMenuOnSelect={true}
                          components={animatedComponents}
                          onChange={this.handleChangeArchive}
                          options={optionArchive}
                          placeholder={this.state.holder_archive}
                        />
                      </Col>
                    </Row>
                    {/* <input type="checkbox" checked="checked" /> */}
                  
                    {/* {
                      this.state.files_preview.map((element,idx)=>(
                        <Row className="mt-2">
                          <Col md="12" sm="12" lg="4" style={{textAlign:"center"}} >
                            <img style={{width:"900px",heigth:"300px",marginLeft:"30px"}} class="preview" src={element}/>
                          </Col>
                        </Row>
                      ))
                    } */}
                    
                    <Row className="justify-content-start mt-5">
                     
                      <Col md="6">
                        <Button onClick={this.handleRetour}  color="secondary" block className="b-lg">
                          Annuler
                        </Button>
                      </Col>
                      <Col md="6">
                        <Button id="initial" onClick={this.handleUpdateEquipe} color="success" block className="b-lg" >
                          Modifier détails de l'équipe
                        </Button>
                        {/* <Button id="chargement" color="info" className="b-lg" type="button" disabled>
                        <span className="spinner-border spinner-border-sm" role="status" aria-hidden="true"> </span>
                          Envoi...
                        </Button> */}
                      </Col>
                    </Row>
                  </CardBody>
                </Card>
              </Col>
            </Row>
          </Container>
        </div>
             
      </React.Fragment>
    </>
        )}

  }
  export default ModifierEquipe